import React from "react"
import { getTheme } from "../myHooks/theme"

export default function Posts({posts=[],onClick,changePost}){
    var classOfList = posts.length > 0 ? 'd-block p-0 mx-5' : 'd-none'
    // console.log(posts)
    const theme = getTheme()
    
    return <ul key='posts' className={classOfList} style={{listStyle:"none"}}>
        <h2 className={`card my-3 ${theme}`}>Posts</h2>
        {
            posts.map(post=>{
                return (
                <li key={post.id} className={`card my-3 ${theme}`} id={post.id} onClick={onClick}>
                    <div className="card-body text-capitalize">
                        {/* <i className="fa fa-pencil float-end h3" aria-hidden="true" onClick={changePost}></i> */}
                        <h4 className="card-title">{post.title}</h4>
                        <p className="card-text">{post.body}</p>
                    </div>
                </li>)
            })
        }
    </ul>
}


