import { useState } from "react"

export default function States() {
    const mainSiteName = 'https://jsonplaceholder.typicode.com/'
    const [users, setUsers] = useState([])
    const [loaderClass, setLoaderClass] = useState('')
    const [loaderDots, setLoaderDots] = useState('')
    var dots = ''
    var loadInterval

    function startLoad(bool = true) {
        if (bool) {
            setLoaderClass("spinner m-auto d-block text-center")
            loadInterval = setInterval(() => {
                // console.log(dots);
                setLoaderDots(dots.length === 3 ? '' : dots + '.')
            }, 300);
        } else {
            // setLoaderDots('')
            setLoaderClass("m-auto d-none text-center")
            clearInterval(loadInterval)
        }
    }

    const [posts, setPosts] = useState([])

    function getPosts({ target }) {
        startLoad()
        fetch(mainSiteName + 'posts?userId=' + target.offsetParent.id).then(res => res.json()).then(data => setPosts(data)).catch(setPosts([])).finally(() => startLoad(false))
    }
    const [comments, setComments] = useState([])

    function getComments({ target }) {
        startLoad()
        fetch(mainSiteName + 'comments?postId=' + target.offsetParent.id).then(res => res.json()).then(data => setComments(data)).catch(setComments([])).finally(() => startLoad(false))
    }
    // function changePost(e){
    //   e.stopPropagation()
    //   let li = e.target.offsetParent,
    //   defaultPost = posts.find(val=>`${val.id}` === li.id),
    //   card = li.innerHTML.slice()
    //   console.log(defaultPost);
    //   reactDom.unmountComponentAtNode(li)
    //   reactDom.render(<form onClick={e=>e.stopPropagation()}>
    //     <textarea id='renamePostTitle' className='form-control' defaultValue={defaultPost.title}/>
    //     <textarea id='renamePostBody' className='form-control my-2' defaultValue={defaultPost.body}/>
    //     <input type="submit" value="Redact" onClick={e=>e.stopPropagation()} className='btn btn-primary'/>
    //   </form>,li)
    //   li.firstChild.children.renamePostTitle.focus()
    //   li.onclick = e => e.stopPropagation() 
    //   li.firstChild.onsubmit = e =>{
    //     e.preventDefault()
    //     let post = defaultPost,
    //     form = e.target,
    //     newPostsList = posts.map(value=>value.id === post.id ? post : value)
    //     post.title = form.children.renamePostTitle.value
    //     post.body = form.children.renamePostBody.value
    //     // setPosts()
    //     // reactDom.render(
    //     // <div className="card-body text-capitalize">
    //     //     <i className="fa fa-pencil float-end h3" aria-hidden="true" onClick={changePost}></i>
    //     //     <h4 className="card-title">{form.children.renamePostTitle.value}</h4>
    //     //     <p className="card-text">{form.children.renamePostBody.value}</p>
    //     // </div>,li)
    //     setPosts(newPostsList)
    //     console.log(newPostsList);
    //     console.log(form.children.renamePostTitle.value)
    //     console.log(`card`, typeof card);
    //     li.innerHTML = card
    //   } 
    // }
    return { getComments, getPosts, users, comments, posts, startLoad, loaderClass, loaderDots, setUsers, mainSiteName }
}


