/* eslint-disable */
import React, { useEffect } from "react";
import './Styles/font-awesome-4.7.0/css/font-awesome.min.css'
import './Styles/bootstrap.min.css'
import Users from "./Components/Users";
import Posts from "./Components/Posts";
import Comments from "./Components/Comments";
// import reactDom from "react-dom";
import States from "./myHooks/States";
import { getTheme } from "./myHooks/theme";
import './Styles/App.css'
import ThemeChanger from "./Components/ThemeChanger";

function $(tag, mother = document){mother.querySelector(tag)}
function App() {
  const {getComments,getPosts,users,comments,posts,startLoad,loaderClass,loaderDots,setUsers,mainSiteName} = States()
  useEffect(()=>{
  startLoad()
  fetch(mainSiteName+'users').then(res=>res.json()).then(data=>setUsers(data)).catch(setUsers([])).finally(()=>startLoad(false))
  // return startLoad(false)
},[])

const theme= getTheme();
  return (
  <div className={`container-fluid ${theme}`}>
          <ThemeChanger/>
      <div className={loaderClass}>
          <div className="spinner-border m-auto" role="status" aria-hidden="true"></div>
      <strong className="mx-3">Loading {loaderDots}</strong> 
      </div>
      <div className='d-flex justify-content-around text-center'>
          <Users users={users} onClick={getPosts}/>
          <Posts posts={posts} onClick={getComments}/>
          <Comments comments={comments}/>
      </div>
   </div>
  );
}

export default App;



