import { getTheme } from "../myHooks/theme"
import React from "react";
export default function Users({users=[],onClick}){
    const theme = getTheme()
    if(users.length < 1) return <h1 className={`card my-3 ${theme}`}>No users found</h1>
    return <div id="users">
        <h2 className={`card my-3 ${theme}`}>Users</h2>
        <ul key='usersList' style={{listStyle:"none"}}>
        {
            users.map(user=>{
                return (
                    <li key={user.id} id={user.id} className={`card my-3 ${theme}`} onClick={onClick}>
                        <div className="card-body px-2">
                            <h4 className="card-title">{user.name}</h4>
                            <a href={`https://${user.email.toLowerCase()}`} target='_blank' className="card-text">{user.email}</a>
                        </div>
                    </li>)})
        }
    </ul>
    </div>
}


