import React,{useState} from "react"
import {getTheme} from "../myHooks/theme"
import '../Styles/bootstrap.min.css'
export default function ThemeChanger(){
    const {theme,setTheme} = getTheme(true);
    // const {button,setButton} = useState((theme === 'dark'?'o':'|'))
    const [classOfThemeChanger, setClassOfThemeChanger] = useState('d-flex')
    window.onkeydown = function({altKey,ctrlKey,key}){
        if(altKey && ctrlKey) {
            if(key === 't') setTheme(theme === 'dark' ? 'light' : 'dark')
            else if(key === 'h') setClassOfThemeChanger(classOfThemeChanger === 'd-flex' ? 'd-none' : 'd-flex')
        }
      }
      
    return (
        <div>
            <div className={`${classOfThemeChanger} justify-content-center`}>
                <label className="switch my-1 mx-3">
                    <input type="checkbox" id='switchBox' onChange={()=>{
                        if(theme === 'dark')setTheme('light')
                        else setTheme('dark')
                    }} checked={theme === 'light'}/>
                    <span className="slider round"></span>
                </label>
                <h2 id='themeTip' style={{color:'grey'}}>
                    <span className='text-danger'>!!!</span> To change theme to
                        {theme === 'dark' ? <span className='text-white'> light </span> : <span className='text-dark'> dark </span>}
                        press buttons <span className='text-info'>CTRL+ALT+T</span> 
                    </h2>
                </div>
            <h1 className='text-center h3' style={{color:'grey'}}>You can {classOfThemeChanger === 'd-flex' ? 'hide' : 'show'} tips with <span  className='text-info h2'>CTRL+ALT+H</span></h1>
            </div>)
}


